import React, { useState } from "react";
import "./styles.css";
// import 'bootstrap/min.css'

export default function App() {
  const [data, setData] = useState(""); //input
  const [ans, setAns] = useState("");
  function handleInput(e) {
    setData(e.target.value);
  }
  function Calculate() {
      setAns(eval(data));
  }
  return (
    <div className="App" style={{padding:"30px", backgroundColor: "black"}}>
      <input onChange={handleInput} style={{color:"#ff3300"}}/>
      <button
        type="submit"
        onClick={Calculate}
        className="waves-effect waves-teal btn-flat pink"
      >
        Calculate
      </button>
      <h3 style={{color:"#0055ff"}}>{ans}</h3>
    </div>
  );
}
